import { Component } from '@angular/core';
declare var jquery:any;
declare var $ :any;

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent {
  title = 'app';
  constructor(){
    this.toggleTitle();  
  }
  toggleTitle(){
    alert('hii');
    $('.carousel').carousel({
      interval: 6000
    });
  }
  // toggleTitle(){
  //   $('.title').slideToggle(); //
  // }
}



